<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :label-position="labelPosition"
          label-width="100px"
          :model="mail"
          class="formStyle text_common"
          size="mini"
          ref="mail"
        >
          <el-form-item label="邮件类型" prop="gmail_type">
            <el-radio-group v-model="mail.gmail_type">
              <el-radio :label="1">全局邮件</el-radio>
              <el-radio :label="2">公告邮件</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="过期时间(秒)"
            prop="expire_time"
            v-show="mail.gmail_type == 2"
          >
            <el-input
              v-model.number="mail.expire_time"
              auto-complete="false"
            ></el-input>
          </el-form-item>
          <el-form-item label="邮件标题" prop="title" :required="true">
            <el-input v-model="mail.title"></el-input>
          </el-form-item>
          <el-form-item label="邮件内容" prop="content" :required="true">
            <el-input
              type="textarea"
              :autosize="{ minRows: 6, maxRows: 10 }"
              :maxlength="200"
              :show-word-limit="true"
              v-model="mail.content"
              :clearable="true"
            ></el-input>
          </el-form-item>
          <el-form-item label="发送类型" prop="uuid" :required="pushOne">
            <el-radio-group v-model="pushOne">
              <el-radio :label="true">个人邮件</el-radio>
              <el-radio :label="false">全服邮件</el-radio>
            </el-radio-group>
            <br />
            <label v-show="pushOne">指定用户ID &nbsp;&nbsp;</label
            ><el-input
              v-show="pushOne"
              v-model="mail.uuid"
              style="width:200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="内容类型" prop="type">
            <el-radio-group v-model="mail.type">
              <el-radio :label="0">普通邮件</el-radio>
              <el-radio :label="1">附件邮件</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-button
            type="primary"
            v-show="mail.type === 1"
            style="margin-left:100px"
            size="mini"
            @click="addAppendix"
            >新增</el-button
          >
          <el-form-item
            label="附件列表"
            prop="appendix"
            v-show="mail.type === 1"
          >
            <template>
              <el-table :data="mail.appendix" border style="width:100%">
                <el-table-column prop="type" label="类型" width="100">
                  <template slot-scope="scope">
                    <el-input
                      v-model.number="scope.row.type"
                      placeholder="类型"
                    ></el-input>
                  </template>
                </el-table-column>
                <el-table-column prop="id" label="道具id" width="109">
                  <template slot-scope="scope">
                    <el-input
                      v-model.number="scope.row.id"
                      placeholder="道具id"
                    ></el-input>
                  </template>
                </el-table-column>
                <el-table-column prop="num" label="数量" width="100">
                  <template slot-scope="scope">
                    <el-input
                      v-model.number="scope.row.num"
                      placeholder="数量"
                    ></el-input>
                  </template>
                </el-table-column>
                <el-table-column label="操作" width="100">
                  <template slot-scope="scope">
                    <el-button
                      @click.native.prevent="deleteRow(scope.$index)"
                      type="danger"
                      size="small"
                    >
                      移除
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
            </template>
          </el-form-item>
          <el-form-item label="按钮类型" prop="btnClickAction">
            <!-- <el-input v-model.number="mail.btnClickAction"></el-input> -->
            <el-radio-group v-model="mail.btnClickAction">
              <el-radio >普通</el-radio>
              <el-radio label="0">充值</el-radio>
              <el-radio label="1">抽奖</el-radio>
              <el-radio label="2">领取</el-radio>
              <el-radio label="3">邀请</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="邮件内容点击" prop="contClickAction">
            <!-- <el-input v-model.number="mail.contClickAction"></el-input> -->
            <el-radio-group v-model="mail.contClickAction">
              <el-radio label="0">可以</el-radio>
              <el-radio label="1">不可以</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="背景图" prop="bgContentImg">
            <!-- <el-input v-model.number="mail.bgContentImg"></el-input> -->
            <el-radio-group v-model="mail.bgContentImg">
              <el-radio :label="0">没有</el-radio>
              <el-radio :label="1">有</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="邮件图标" prop="mailIco">
            <!-- <el-input v-model.number="mail.mailIco"></el-input> -->
            <el-radio-group v-model="mail.mailIco">
              <el-radio >默认图标</el-radio>
              <el-radio label="0">提示充值</el-radio>
              <el-radio label="1">提示抽奖</el-radio>
              <el-radio label="2">提示领取金币</el-radio>
              <el-radio label="3">邀请好友</el-radio>
              <el-radio label="4">系统消息</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="能否删除" prop="isDel">
            <el-radio-group v-model="mail.isDel">
              <el-radio :label="0">不能删除</el-radio>
              <el-radio :label="1">能删除</el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" size="medium" @click="onSubmit"
              >提交</el-button
            >
            <el-button type="danger" size="medium" @click="resetForm('mail')"
              >重置</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      mail: {
        gmail_type: null,
        expire_time: null,
        title: null,
        content: null,
        uuid: null,
        type: 0, //0.普通 1.邮件附件
        appendix: [], //配置示例："appendix":[{"type":0,"id":0,"num":99}]
        contClickAction: null,
        bgContentImg: null,
        mailIco: null,
        isDel: null //0=不能删除，1=能删除（默认)
      },
      pushOne: true
    };
  },
  methods: {
    onSubmit() {
      if (!this.mail.title || !this.mail.content) {
        this.$message({
          showClose: true,
          message: "标题和内容不能为空",
          type: "error"
        });
        return;
      }
      if (this.mail.type == 0) {
        this.mail.appendix = [];
      }
      var that = this;
      this.$api.mail.sendMail(this.mail).then(res => {
        if (res.data.success === true) {
          this.$message({
            showClose: true,
            message: "发送邮件成功",
            type: "success"
          });
          that.$refs.mail.resetFields();
        } else {
          this.$message({
            showClose: true,
            message: "发送邮件失败",
            type: "error"
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    addAppendix() {
      this.mail.appendix.push({ type: "", id: "", num: "" });
    },
    deleteRow(id) {
      this.mail.appendix.splice(id, 1);
    }
  },
  watch: {
    mail: {
      //深度监听，可监听到对象、数组的变化
      handler(val, oldVal) {
        if (val.gmail_type != 2) {
          val.isDel = 0;
        }
      },
      immediate: true,
      deep: true //true 深度监听
    }
  }
};
</script>

<style lang="less" scoped>
.form_container {
  border: #eef1f6 2px solid;
  width: 65%;
  margin: 2% 10%;
}
.formStyle {
  width: 60%;
  margin: auto auto;
  padding: 60px 0;
}
</style>



